.container{
    background-color: #ffffff;
    padding:1rem 2rem;
    border-radius: 5px;
    width:90%;
    margin: 2rem auto;
}

@media screen and (min-width:992px){
    .container{
        width: 40%;
    }
}

@media screen and (min-width:1200px){
    .container{
        width: 35%;
    }
}